import "./assets/css/progress-bar.css!";
import {AWidget} from "./abstract/a-widget";
import {EBasicColorStatus} from "../component/enum/e-basic-color-status";
import {EventEmitter} from "./../core/event-emitter";
import jquery = require("jquery");
import {EProgressStyle} from "./enum/e-progressbar";

/**
 * Componene que exibe uma barra de progresso 
 * 
 * <img src="../../doc_assets/progress_bar_exemple.png">
 * 
 * @example
 * 
 *  let progress: ProgressBar = new ProgressBar()
 *      .setProgress(10); // Atualiza a barra para 10% de progresso
 *      .setColor(EBasicColorStatus.PRIMARY) // Seta a cor do progress para a cor principal
 * 
 * @export
 * @extends {AWidget}
 * @memberof module:mvcomponents/widget
 */
export class ProgressBar extends AWidget {
	private current: number;
	private duration:number;
	private animationDisabled:boolean;
	public onProgress:EventEmitter<number> = new EventEmitter<number>();
	constructor(progress: number = 0,duration:number = 600) {
		super("div", `<div class="progress" style="margin-bottom:4px">
							<div class="barra progress-bar no-transition" role="progressbar" aria-valuenow="0" aria-valuemin="0" aria-valuemax="100" style="width: ${progress}%">
								<span class="sr-only">0% Completo</span>
							</div>
						</div>`);
		this.current = progress;
		this.duration = duration;
		jquery(this.element).addClass("ProgressBar col-xs-12 col-sm-12 form-group");//.setStyles({"width":"100%","height":"20px","float":"left"});
	}

	/**
	 * Atualiza o progresso da barra 
	 * 
	 * @param {number} progress Progresso atual, na escala de 0-100;
	 */
	public setProgress(progress: number): ProgressBar {
		this.current = progress;
		jquery(this.element).find("div.barra:first")
			.attr("aria-valuenow", progress);
		if(this.animationDisabled){
			jquery(this.element).find("div.barra:first")
				.css("width",`${progress}%`);
			this.onProgress.emit(this.getProgress());
		}else{			
			(<any>jquery(this.element).find("div.barra:first"))
				.animate({"width":`${progress}%`},this.duration,() => {
					this.onProgress.emit(this.getProgress());
			});
		}
		return this;
	}

	public setDuration(duration:number):ProgressBar{
		this.duration = duration;
		return this;
	}

	/**
	 * Obtém qual o progresso que está sendo atualizado
	 * 
	 * @returns {number}
	 */
	public getProgress(): number {
		return this.current;
	}
	
	private getInnerProgressBar(){
		return jquery(this.element).find(".progress:first");
	}

	public disableAnimation(off:boolean):ProgressBar{
		this.animationDisabled = off;
		return this;
	}

	public setStyle(style:EProgressStyle):ProgressBar{
		if(style===EProgressStyle.STRIPED){
			this.getInnerProgressBar().addClass("progress-striped active");
		}else if(style===EProgressStyle.SOLID){
			this.getInnerProgressBar().removeClass("progress-striped active");
		}
		return this;
	}
	public setHeight(height:string):ProgressBar{
		this.getInnerProgressBar().css({height:height});
		return this;
	}

	/**
	 * Informa a cor da barra de progresso 
	 * 
	 * @param {EBasicColorStatus} bgc
	 */
	public setColor(bgc: EBasicColorStatus): ProgressBar {
		jquery(this.element)
			.find("div.barra")
			.removeAttr("class")
			.addClass(`barra progress-bar progress-bar-${EBasicColorStatus[bgc].toLowerCase()}`);
		return this;
	}

	private setToolTip(tooltip: string): ProgressBar {
		jquery(this.element).attr("alt", tooltip);
		return this;
	}
	
	private setLabel(nlabel: string): ProgressBar {
		this.setToolTip(nlabel);
		return this;
	}
}
